<template>
  <tr >
    <th scope="row">{{ index + 1 }}</th>
    <td><img class="image-cart-table" :src="product.imageAddress" /></td>
    <td @click="transmit_Product(product)">{{ product.productName }}</td>
    <td>{{ product.price }}</td>
    <td>
      <input type="number" v-model="quantity" class="input-quantity-cart" />
    </td>
    <td>{{ product.price * quantity }} VNĐ</td>
    <td>
      <button class="btn btn-danger" @click="remove_cart_item(index)">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          class="bi bi-trash"
          viewBox="0 0 16 16"
        >
          <path
            d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"
          />
          <path
            fill-rule="evenodd"
            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
          />
        </svg>
      </button>
    </td>
  </tr>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: {
    product: Object,
    index: Number,
    transmit_Product: Function,
  },
  data() {
    return {
      quantity: this.product.quantity,
    };
  },
  methods: {
    ...mapActions(["updateCartQuantity","remove_cart_item"]),
  },
  watch: {
    quantity: function () {
        if(!isNaN(parseInt(this.quantity))){
           
                this.updateCartQuantity({ index: this.index, quantity: this.quantity });
        }
         console.log(isNaN(parseInt(this.quantity)))
        
      
    },
  },
};
</script>

<style>
.image-cart-table {
  width: 100px;
  height: 100px;
}
.input-quantity-cart {
  width: 50px;
}
</style>